import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { Button, message, Form, Checkbox } from 'antd';
import PropTypes from 'prop-types';
import './index.less';
import FormCom from '../searchCom/formCom';
import { formItemLayout } from '../../utils/index';
import { setLoginInfo, getLoginInfo, setUserInfo } from '../../utils/localStorageSet';

class Login extends React.Component {
    static propTypes = {
        history: PropTypes.object,
    };

    static defaultProps = {
        history: {},
    };

    formRef = React.createRef();

    constructor(props) {
        super(props);
        this.state = {
            checked: false,
        };
    }

    componentDidMount() {
        this.initData();
    }

    initData = () => {
        //  -- cookie
        const loginInfo = getLoginInfo();
        this.formRef.current.setFieldsValue({
            name: loginInfo.name,
            password: loginInfo.password,
        });
        //   
    };

    onEnterFun = () => {
        this.formRef.current.submit();
    };

    onCheckedChange = (e) => {
        this.setState({
            checked: e.target.checked,
        });
    };

    onFinish = (values) => {
        const { checked } = this.state;
        const { history } = this.props;
        if (checked) {
            setLoginInfo(values);
        }
        switch (values.name) {
            case '1':
                // 6
                setUserInfo({
                    createTime: '2021-10-07 12:00:01',
                    departments: '2,4,7',
                    director: '',
                    id: 6,
                    jobTitle: '',
                    mobile: '15122223333',
                    name: '6',
                    status: 1,
                });
                break;
            case '2':
                // 2
                setUserInfo({
                    createTime: '2021-10-07 12:00:01',
                    departments: '2,3,4',
                    director: '',
                    id: 2,
                    jobTitle: '',
                    mobile: '15122225555',
                    name: '2',
                    status: 1,
                });
                break;
            case '3':
                // 1
                setUserInfo({
                    createTime: '2021-10-07 12:00:01',
                    departments: '1,2,3',
                    director: '',
                    id: 6,
                    jobTitle: '',
                    mobile: '15122223333',
                    name: '1',
                    status: 1,
                });
                break;
            default:
                break;
        }
        history.push('/');
    };

    render() {
        const { checked } = this.state;
        const formArr = [
            {
                type: 'input',
                name: 'name',
                label: '',
                placeholder: '...',
                required: true,
                className: 'name_style',
                onEnter: this.onEnterFun,
                maxLength: 10,
            },
            {
                type: 'password',
                name: 'password',
                label: '',
                inpuType: 'password',
                placeholder: '...',
                required: true,
                className: 'name_style',
                onEnter: this.onEnterFun,
                maxLength: 10,
            },
        ];
        return (
            <div className="loagin_com_box">
                <div className="login_title">
                    <span></span> <span className="common_color"></span>
                </div>
                <Form ref={this.formRef} onFinish={this.onFinish} {...formItemLayout(6, 14)}>
                    <FormCom searchArr={formArr} />
                    <div className="set_possword">
                        <Checkbox checked={checked} onChange={this.onCheckedChange}>
                            
                        </Checkbox>
                        <div className="forget common_color"></div>
                    </div>
                    <Button type="primary" size="large" htmlType="submit" block>
                         
                    </Button>
                </Form>
            </div>
        );
    }
}
const mapState = (state = {}) => ({});
const mapDispatch = (dispatch) => ({});
export default connect(mapState, mapDispatch)(withRouter(Login));
